* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}
.hero {
  padding-top: 56px; /* Adjust this value according to the height of your navbar */
}


.login {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background-image: url('https://img.freepik.com/free-photo/solar-panels-roof-solar-cell_335224-1324.jpg?w=900&t=st=1672744315~exp=1672744915~hmac=9c4992d9819da54508620cb54f003ddb1ad6c8c645ab5fb37630f8de53cdabc0');
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
}

.login .loginContainer {
  padding: 60px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(22, 24, 47, 1) 0%,
    rgba(38, 20, 72, 0.9) 59%,
    rgba(17, 27, 75, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}

button {
  border: none;
  outline: none;
  width: 20%;
  padding: 15px 0;
  color: #000;
  font-size: 16px;
  letter-spacing: 1px;
  background: #FFC312;
  cursor: pointer;  
}

.login .loginContainer h3{
color: white;
}

.input-group-prepend span{
  width: 50px;
  background-color: #FFC312;
  color: black;
  border:0 !important;
  }

.login .loginContainer label {
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.login .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
}

.btnContainer {
  width: 100%;
  padding: 24px 0px;
}

.login .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}

.login .loginContainer .btnContainer p span {
  color: yellow;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}

.login .loginContainer .btnContainer p span:hover {
  color: red;
}

.login .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  .loginContainer {
      width: 80%;
      margin: auto;
  }

  .btnContainer {
      text-align: center;
      position: relative; /* Add relative positioning */
  }

  .btnContainer p {
      /* Adjust position of the paragraph text */
      position: absolute;
      bottom: -40px; /* Adjust the distance from the buttons */
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      text-align: center;
  }

  .btnContainer button {
      /* Adjust button styles for smaller screens */
      /* For example, you can change the width or font size */
      width: 100%;
      font-size: 16px;
  }
}



