.stickyTop{
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000; 
}

.menu3 li:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  display: block;
  transition: all 0.3s ease;
  bottom: 25%;
}
.menu3 li:hover::after {
  width: 100%;
  height: 1px;
  background-color: #fff;
}

.menu3 li {
  /* display: block; */
