  transition: all 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

/* Width */
.sidebar {
  width: 249px; /* Adjust the width as needed */
  overflow-y: auto;
}

/* Style the scrollbar */
.sidebar::-webkit-scrollbar {
  width: 5px; /* Width of the scrollbar */
}

/* Track */
.sidebar::-webkit-scrollbar-track {
  background: #f1f1f1; /* Color of the track */
}

/* Handle */
.sidebar::-webkit-scrollbar-thumb {
  background: #888; /* Color of the scrollbar handle */
}

/* Handle on hover */
.sidebar::-webkit-scrollbar-thumb:hover {
  background: #555; /* Color of the scrollbar handle on hover */
}

/* For Firefox */
.sidebar {
  scrollbar-width: thin; /* Width of the scrollbar */
  scrollbar-color: #888 #f1f1f1; /* Color of the scrollbar handle and track */
}

/* For Edge */
.sidebar {
  -ms-overflow-style: none; /* Hide scrollbar */
}

.sidebar::-webkit-scrollbar {
  display: none; /* Hide scrollbar in WebKit-based browsers */
}


.chart-container {
  border: 2px solid  #17a2b8;
  padding: 20px 3px 3px 2px; /* Adjust padding to make the border closer to the left side */
  width: fit-content; /* Adjust the width as per your requirement */
  margin: 20px 5px 5px 5px; /* Center the container horizontally and add 20px margin from the top */
  border-radius: 5px;
}









